// 
import 'reset-css'
// css
import './common/common.css'

import { adaptZoom, default as zoom } from '@/util/zoom/zoom'

import config from '@/config'
import apis from '@/apis'

import globalConfig from './base.config'

export default {
  /**
   *  app
   *  @param    {Object}  app 
   */
  load(app, width, height) {
    const me = this

    // api
    config.merge(globalConfig)
    // api
    config.merge(app.config)

    // api
    apis.merge(app.apis)

    me.initZoom(width, height)

    // app
    app.init()
  },
  /**
   *  configzoom
   */
  initZoom(width, height) {
    if(config.get('zoom')) {
      // true
      adaptZoom(width, height)
      
      window.addEventListener('resize', () => {
        console.log(width, height)
        zoom(width, height)
      })
      zoom(width, height)
    }
  }
}



