const search = global.location.search

export default {
  // mockmockmockjs
  mock: search.indexOf('mock') == -1,
  // websocket()
  mockInterval: 3500,
  // ()
  fetchInterval: 3000,
  // proxyproxyproxyurlurl
  proxy: search.indexOf('proxy') !== -1,
  // proxyfalseajax
  host: '/api',
  // websocketproxyfalsewebsocket
  websocketHost: 'ws://online.com',
  // proxytrueajax,
  proxyHost: 'http://192.168.1.199:3000/api',
  // websocketproxytruewebsocket,
  websocketProxyHost: 'ws://offline.com',
  // zoomzoomtransform
  zoom: true,
  // 
  pageWidth: 1920,
  // 
  pageHeight: 1080
}


