/**
 *  
 *  @param    {*}  val        
 *  @param    {string}  expectType 
 *  @return   {boolean} truefalse
 */
export function is(val, expectType) {
  return Object.prototype.toString.call(val) === `[object ${expectType}]`
}

/**
 *  Object
 *  @param    {*}  val 
 *  @return   {boolean} trueObjectfalseObject
 */
export function isObject(val) {
  return is(val, 'Object')
}

/**
 *  String
 *  @param    {*}  val 
 *  @return   {boolean} trueStringfalseString
 */
export function isString(val) {
  return is(val, 'String')
}

/**
 *  Function
 *  @param    {*}  val 
 *  @return   {boolean} trueFunctionfalseFunction
 */
export function isFunction(val) {
  return is(val, 'Function')
}

/**
 *  Number
 *  @param    {*}  val 
 *  @return   {boolean} trueNumberfalseNumber
 */
export function isNumber(val) {
  return is(val, 'Number')
}

/**
 *  Boolean
 *  @param    {*}  val 
 *  @return   {boolean} trueBooleanfalseBoolean
 */
export function isBoolean(val) {
  return is(val, 'Boolean')
}

/**
 *  Array
 *  @param    {*}  val 
 *  @return   {boolean} trueArrayfalseArray
 */
export function isArray(val) {
  return is(val, 'Array')
}


