import zrender from 'zrender'

export default zrender.Path.extend({
  type: 'polygon',
  shape: {
    num: 3,
    cx: 0,
    cy: 0,
    radius: 0,
    range: []
  },

  /**
   * 
   * @param  {Object} ctx   
   * @param  {Object} shape  shape 
   * @return {void}   void
   */
  buildPath(ctx, shape) {
    const { num, cx, cy, radius, range } = shape
    // 1. 
    // =====================
    if (range.length === 0) {
      ctx.moveTo(cx, cy - radius)
    } else {
      let angle = 360 / num * Math.PI / 180   
      let actAngle = angle - Math.PI / 2   
      let x = cx + Math.cos(actAngle) * (radius * range[0])
      let y = cy + Math.sin(actAngle) * (radius * range[0])
      ctx.moveTo(x, y)
    }

    // 2. 
    // ================
    this.points = []
    for (let i = 0; i < num; i ++) {
      // 
      let angle = 360 / num * (i + 1) * Math.PI / 180

      // 90
      let actAngle = angle - Math.PI / 2

      // 
      let x, y
      if (range.length === 0) {
        x = cx + Math.cos(actAngle) * radius
        y = cy + Math.sin(actAngle) * radius
      } else {
        x = cx + Math.cos(actAngle) * (radius * range[i])
        y = cy + Math.sin(actAngle) * (radius * range[i])
      }
      ctx.lineTo(x, y)   
      
      this.points.push([x, y])
    }    
    
    ctx.closePath()
  },

  /**
   * 
   *  window.onload 
   * @return {Array} 
   */
  getPoints() {
    return this.points
  }
})



