import $ from 'jquery'
import { errorTooltip, delay } from './util'
import { isFunction, isNumber, isArray } from './typeCheck'
import apis from '@/apis'
import config from '@/config'
import ReconnectingWebSocket from 'reconnecting-websocket'

/**
 * AJAX
 *  json.config.contentType'application/json; charset=UTF-8'
 *  .config.contentType'multipart/form-data; charset=UTF-8'
 * 
 * @export 
 * @param {string} apiName 
 * @param {object} [data={}]  
 * @param {function} callback 
 * @returns {Boolean} false
 */
export function fetch(apiName, data = {}, callback) {
  if(arguments.length === 2 && isFunction(data)) {
    // apiNamecallback
    callback = data
    data = {}
  }
  const ajaxConfig = apis.config(apiName)
  const extraAjaxConfig = {}
  let ajaxData = apis.filterData(apiName, data)
  if (ajaxConfig.contentType) {
    const contentType = ajaxConfig.contentType
    if (contentType.indexOf('application/json') != -1) {
      // application/json
      ajaxData = JSON.stringify(ajaxData)
    } else if (contentType.indexOf('multipart/form-data') != -1) {
      // formData
      // contentTypefalsexhrboundary
      extraAjaxConfig.contentType = false
      // xhrformDatajquery
      extraAjaxConfig.processData = false
      const formData = new FormData()
      Object.keys(ajaxData)
        .forEach(key => {
          const val = ajaxData[key]
          if (isArray(val)) {
            val.forEach(val => {
              formData.append(key, val)
            })
          } else {
            formData.append(key, val)
          }
        })
      extraAjaxConfig.data = formData
    }
  }
  return $.ajax({
    url: apis.url(apiName, data),
    dataType: 'json',
    data: ajaxData,
    ...ajaxConfig,
    ...extraAjaxConfig
  }).done((response) => {
    if(!response.code){
      return errorTooltip( `:${ response.msg }` )
    }
    callback && callback(response.result)
  }).fail(() => {
    return errorTooltip( ': ' )
  })
}

/**
 *  api
 *  @param    {string}   apiName api
 *  @param    {object=}   data    
 *  @param    {Function}    cb    
 *  @param    {number=}    interval    
 *  @return   {Object}   
 */
export function fetchInterval(apiName, data, cb, interval) {
  const argumentsLength = arguments.length
  const defaultInterval = config.get('fetchInterval')
  if (argumentsLength === 2) {
    // 2
    // datainterval
    cb = data
    data = null
    interval = null
  } else if (argumentsLength === 3) {
    // 3
    if (isFunction(cb)) {
      // interval
      interval = null
    } else if (isNumber(cb)) {
      // data
      interval = cb
      cb = data
      data = null
    }
  }

  data = data || {}
  interval = interval || defaultInterval

  let hasClosed = false
  const delayFetchInterval = delay(
    function() {
      if (hasClosed) {
        return
      }
      fetch(apiName, data, cb)
        .always(delayFetchInterval)
    },
    interval
  )
  delayFetchInterval(true)
  return {
    // 
    close() {
      hasClosed = true
    }
  }
}

/**
 *  websocket
 *  @param    {string}   apiName api
 *  @param    {Object=}   data    
 *  @param    {Function} cb      
 *  @return   {Websocket}   websocket
 */
export function fetchSocket(apiName, data, cb) {
  if (arguments.length === 2 && isFunction(data)) {
    // 22cb
    cb = data
    data = {}
  }
  if (config.get('mock')) {
    // mockfetchInterval
    const fakeWs = fetchInterval(apiName, data, cb)
    return fakeWs
  }
  // mockwebsocket
  const ws = new ReconnectingWebSocket(
    apis.url(apiName, data), []
  )
  ws.onmessage = cb
  ws.onclose = function () {
    this.close()
  }
  // websocket
  window.addEventListener(
    'unload',
    function close() {
      window.removeEventListener('unload', close)
      ws.close()
    }
  )
  return ws
}



