import d3 from 'd3'
/**
 * 
 * @param  {Number} len 
 * @return {String} 
 */
export function randomString(len) {
  let s = ''
  let randomchar = function () {
    let n = Math.floor(Math.random() * 62)
    if (n < 10) {
    // 1-10
      return n 
    }
    if (n < 36) {
    // A-Z
      return String.fromCharCode(n + 55) 
    }
    // a-z
    return String.fromCharCode(n + 61) 
  }
  while (s.length < len) { 
    s += randomchar() 
  }
  return s
}

/**
 * 
 * @param  {Object} obj 
 * @return {Object} 
 */
export function deepClone(obj) {
  let str, newobj = obj.constructor === Array ? [] : {}
  if (typeof obj !== 'object') {
    return
  } else if (window.JSON) {
    str = JSON.stringify(obj)
    newobj = JSON.parse(str)
  } else {
    for (let i in obj) {
      if (Object.prototype.hasOwnProperty.call(obj, i)) {
        newobj[i] = typeof obj[i] === 'object' 
          ? deepClone(obj[i]) 
          : obj[i]
      }
    }
  }
  return newobj
}

/**
 * 
 * @param  {Number} index 
 * @return {Array}  20
 */
export function japancolors10(index) {
  let colors = [
    '#FFB11B', '#86C166', '#877F6C', '#2EA9DF', '#FBE251',
    '#F05E1C', '#00AA90', '#77428D', '#C1328E', '#566C73'
  ]
  return colors[index]
}

/**
 * svg defsID
 * 
 * @return ID
 */
export const genSVGDocID = ( () => {
  let id = 1
  return () => {
    let prefix = new Date().valueOf()
    return `hyfe-${prefix}-${id++}`
  }
})()

/**
 *  
 *  @example: ['#2c75e1', '#e953ff']
 *  @param    {array}  color 
 *  @return   {function}  
 */
export const interpolate = (color) => {
  return d3.interpolate(color[0], color[1])
}

/**
 * 
 * @param {document} container 
 * @param {object} object  
 * @return {null} null
 */
export const isNoData = (container, { width, height } ) => {
  container.html('')
  container.append('text')
    .html('') 
    .attr('transform', `translate(${width / 2}, ${height / 2})`)
    .attr('font-size', 22)
    .attr('fill', '#fff')
    .attr('text-anchor', 'middle')
}

/**
 *  
 *  @param    {array}   data      
 *  @param    {number}  shapeMax  
 *  @return   {function}  
 */
export const countScale = (data, shapeMax) => {
  return d3.scale.pow()  
    .domain([0, d3.max(data)])
    .range([0, shapeMax])
    .exponent(0.6)   
}

/**
 *  svg
 *  @param    {string}  selector       
 *  @param    {number}  options.width  
 *  @param    {number}  options.height 
 *  @return   {object}  svg
 */
export const appendSVG = (selector, {width, height}) => {
  let svg = d3.select(selector)
    .append('svg')
    .attr('width', width)
    .attr('height', height)
  return svg    
}

/**
 * 
 *  @return   {array}  x,y
 */
export const getMousePosition = () => {
  let pos = [d3.event.offsetX , d3.event.offsetY - 60]
  return{
    x: pos[0],
    y: pos[1]
  }
}

/**
 * svg
 * @param  {String} selector svg  id
 * @param  {Object} opt      
 *                  width     this.grid.width
 *                  height    this.grid.height
 *                  originX   d3.event.x
 *                  originY   d3.event.y
 * @return {Object}  body 
 */
export function getMouseOffset(selector, opt) {
  let { width, height, originX, originY } = opt
  let bbox = document.getElementById(selector).getBoundingClientRect()
  let svgClientWidth = bbox.width
  let svgClientHeight = bbox.height
  let svgClientOffsetX = bbox.x
  let svgClientOffsetY = bbox.y

  // 
  let xRate = svgClientWidth / width
  let yRate = svgClientHeight / height

  let x, y
  if (originX && originY) {
    x = (originX - svgClientOffsetX) / xRate
    y = (originY - svgClientOffsetY) / yRate
  }

  return {
    x, y, xRate, yRate
  }
}



