import Mock from 'mockjs'
import config from './config'
import { isObject } from './util/typeCheck'

const apiMap = {}

/**
 *  hosthost
 *  @param    {string}  host        
 *  @param    {Boolean=} isWebsocket websocket
 *  @return   {string}  host
 */
function normalizeHost(host, isWebsocket) {
  let hosts = host
  if (hosts && hosts.indexOf('//') !== -1) {
    // 
    return hosts
  }
  if (hosts === null) {
    hosts = ''
  }
  hosts = location.host + hosts
  const normalProtocol = location.protocol
  if (isWebsocket) {
    // websocketws
    let protocol = 'ws://'
    if (normalProtocol === 'https:') {
      // httpswss
      protocol = 'wss://'
    }
    hosts = protocol + hosts
  } else {
    hosts = normalProtocol + '//' + hosts
  }
  return hosts
}

// port
const paramReg = /:([^/\d][^/]+)/g

export default {
  /**
   *  
   *  @param    {Object}  apiConfigMap 
   */
  merge(apiConfigMap) {
    const me = this
    let host
    let websocketHost
    const mock = config.get('mock')
    if(config.get('proxy')) {
      // true
      host = config.get('proxyHost')
      websocketHost = config.get('websocketProxyHost')
    } else {
      // false
      host = normalizeHost(config.get('host'))
      websocketHost = normalizeHost(config.get('websocketHost'), true)
    }
    for(const key in apiConfigMap) {
      if(!Object.prototype.hasOwnProperty.call(apiConfigMap, key)
        || !{}.hasOwnProperty.call(apiConfigMap, key)) {
        continue
      }
      // websocket
      let isWebsocket = false
      const apiConfig = apiConfigMap[key]
      const ajaxConfig = apiConfig.config || {}
      const url = apiConfig.url
      if(apiConfig.isWebsocket === true) {
        // websocket
        isWebsocket = true
      }
      if (!ajaxConfig.type) {
        // methodget
        ajaxConfig.type = 'get'
      }
      ajaxConfig.type = ajaxConfig.type.toLowerCase()
      apiMap[key] = {
        // websocketwebsocketHost
        // host
        url: (isWebsocket ? websocketHost : host) + url,
        config: ajaxConfig
      }
      if(mock && apiConfig.mock) {
        // mockapimock
        Mock.mock(
          me.reg(key),
          ajaxConfig.type,
          apiConfig.mock
        )
      }
    }
  },
  /**
   *  url
   *  @param    {string}  key 
   *  @return   {string}  url
   */
  rawUrl(key) {
    return apiMap[key].url
  },
  /**
   *  url
   *  @param    {string}  key 
   *  @param    {Object=}  data urldata
   *  @return   {string}  url
   */
  url(key, data = {}) {
    const me = this
    const url = me.rawUrl(key)

    return url.replace(
      paramReg,
      (str, paramName) => {
        let val = data[paramName]
        if(val === undefined) {
          val = 'null'
        }
        return global.encodeURIComponent(val)
      }
    )
  },
  /**
   *  url
   *  @param    {string}  key 
   *  @return   {RegExp}  url
   */
  reg(key) {
    const me = this
    const rawUrl = me.rawUrl(key)
    let regStr = rawUrl
      .replace(/\./g, '\\.')
      .replace(paramReg, '[^/]+')
    return new RegExp(`^${regStr}(\\?.*)?$`)
  },
  /**
   *  ajax
   *  @param    {string}  key 
   *  @return   {Object}  ajax
   */
  config(key) {
    return apiMap[key] && apiMap[key].config
  },
  /**
   *  url
   *  @param    {string}  key  
   *  @param    {Object}  data 
   *  @return   {Object}  url
   */
  filterData(key, data) {
    const me = this
    if (!isObject(data)) {
      return data
    }
    const resData = {...data}
    const rawUrl = me.rawUrl(key)
    let execRes
    while(execRes = paramReg.exec(rawUrl)) {
      const paramName = execRes[1]
      delete resData[paramName]
    }
    return resData
  }
}



