import zrender from 'zrender'
import d3 from 'd3'
import _ from 'lodash'
import { Legend } from '../util/legend/index'
import { Tooltip } from '../util/tooltip/index'
import { deepClone } from '../util/util'
import { japancolors10 } from '../util/util'

export default class Radar {
  /**
   * 
   * @param  {Object} option 
   * @return {void}   void
   */
  initGroup(option) {
    let series = option.series
    let { width, height } = this.canvas
    let num = series[0].data.length
    this.num = num

    // 
    // =================
    this.axisGroup = new zrender.Group()
    // 
    for (let i = 0; i < this.axis.ticks; i++) {
      this.axisGroup.add(new zrender.Isogon({
        name: 'axisPolygon',
        shape: {
          n: num,
          x: width / 2,
          y: height / 2
        }
      }))
    }
    for (let d of series[0].data) {
      // 
      this.axisGroup.add(new zrender.Text({
        name: 'axisLabel',
        style: {
          text: d.name,
          opacity: 0
        },
        position: [width / 2, height / 2]
      }))
      // 
      this.axisGroup.add(new zrender.Line({
        name: 'axisLine',
        shape: {
          percent: 0
        }
      }))
      //  
      this.axisGroup.add(new zrender.Circle({
        name: 'axisPoint',
        shape: {
          cx: width / 2,
          cy: height / 2
        },
        style: {
          opacity: 0
        }
      }))
    }
    this.zr.add(this.axisGroup)

    // 
    // ==================
    this.radarContainer = []    
    for (let [i, chunk] of series.entries()) {
      this.radarContainer.push(new zrender.Group({ name: chunk.name }))

      //       
      this.radarContainer[i].add(new zrender.Polygon({
        name: 'radar',
        titleName: chunk.name,
        pointName: chunk.data.map(d => d.name),
        shape: {
          points: (function() {
            let points = []
            for (let j = 0; j < num; j++) {
              points.push([width / 2, height / 2])
            }
            return points
          }())
        }
      }))

      // 
      for (let d of chunk.data) {
        this.radarContainer[i].add(new zrender.Circle({
          name: 'radarPoint',
          legendName: d.name,
          shape: {
            cx: width / 2,
            cy: height / 2
          }
        }))
      }

      this.zr.add(this.radarContainer[i])
    }

    // 
    // ============
    if (this.legendConfig.show) {
      this.legend = new Legend(this.zr, option.legend, this.padding)
      this.legendContainer = this.legend.getGroup()
    }
  }

  /**
   * 
   * 
   * @param  {Object} option 
   * @return {void}   void
   */
  initConfig(option) {
    // 
    // ================
    this.canvas = {
      width: this.zr.getWidth(),
      height: this.zr.getHeight()
    }

    this.padding = {
      left: this.canvas.width * 0.05,
      top: this.canvas.height * 0.15
    }

    // 
    // =============
    this.colors = japancolors10

    // 
    // ==============
    this.isAnimate = false

    // 
    // =========
    this.isInit = false

    this.tooltip = _.merge({
      show: true,
      itemStyle: {
        background: 'rgba(11, 16, 19, .66)',
        borderRadius: 4,
        padding: 8,
        fontColor: '#FCFAF2',
        fontSize: 18,
        formatter: '{value}'
      }
    }, option.tooltip)

    this.axis = _.merge({
      ticks: 5,
      polygonStyle: {
        evenColor: 'transparent',
        oddColor: 'transparent',
        borderColor: '#707C74',
        borderWidth: 1
      },
      labelStyle: {
        fontSize: 12,
        fontWeight: 'normal',
        fontStyle: 'normal',
        fontFamily: 'sans-serif',
        color: '#FCFAF2'
      },
      pointStyle: {
        radius: 4,
        color: '#707C74',
        borderWidth: 1,
        borderColor: '#FFF'
      }
    }, option.axis)

    this.legendConfig = _.merge({
      show: false
    }, option.legend)

    // 
    this.radius = option.radius || '75%'
    let radiusRate = Number(this.radius.replace('%', '')) / 100 / 2
    this.radius = this.canvas.width > this.canvas.height
      ? this.canvas.height * radiusRate
      : this.canvas.width * radiusRate
  }

  constructor(selector) {
    this.zr = zrender.init(selector)

    //  tooltip 
    // ==================
    this.tip = new Tooltip(this.zr, selector)
  }

  /**
   * 
   * @param  {Array}  series 
   * @param  {Number} minVal 
   * @param  {Number} maxVal 
   * @return {void}  void
   */
  createScale(series, minVal, maxVal) {
    let arr = []
    for (let chunk of series) {
      for (let d of chunk.data) {
        arr.push(d.value)
      }
    }

    let min = minVal || 0
    let max = maxVal || d3.max(arr)

    let scale = d3.scale.linear()
      .domain([min, max])
      .range([0, this.radius * 0.9])

    return scale
  }

  /**
   * 
   * @param  {Number} num     
   * @param  {Number} cx      
   * @param  {Number} cy      
   * @param  {Number} radius  
   * @param  {Array}  range   
   * @return {Array}  points
   */
  createPoints(num, cx, cy, radius, range = []) {
    let points = []
    for (let i = 0; i < num; i ++) {
      // 
      let angle = 360 / num * (i + 1) * Math.PI / 180

      // 90
      let actAngle = angle - Math.PI / 2
      
      // 
      let x, y
      if (range.length === 0) {
        x = cx + Math.cos(actAngle) * radius
        y = cy + Math.sin(actAngle) * radius
      } else {
        x = cx + Math.cos(actAngle) * (radius * range[i])
        y = cy + Math.sin(actAngle) * (radius * range[i])
      }
           
      points.push([x, y])
    }   

    return points
  }

  /**
   * 
   * @return {void}  void
   */
  initAxis() {
    let axisGroup = this.axisGroup.children()
    let polygonGroup = axisGroup.filter(d => d.name === 'axisPolygon')
    let labelGroup = axisGroup.filter(d => d.name === 'axisLabel')
    let pointGroup = axisGroup.filter(d => d.name === 'axisPoint')
    let lineGroup = axisGroup.filter(d => d.name === 'axisLine')

    // 
    // ===========

    // 
    let range = Array(this.axis.ticks)
    for (let i = 0; i < this.axis.ticks; i++) {
      range[i] = i * (this.radius / (this.axis.ticks - 1))
    }
    range = range.reverse()

    // 
    for (let [i, polygon] of polygonGroup.entries()) {
      // 
      this.isAnimate = true
      polygon
        .attr({
          style: {
            fill: i % 2 === 0
              ? this.axis.polygonStyle.evenColor
              : this.axis.polygonStyle.oddColor,
            stroke: this.axis.polygonStyle.borderColor,
            lineWidth: this.axis.polygonStyle.borderWidth
          }
        })
        .animate('shape', false)
        .when(500, { r: range[i] })
        .start('elasticOut')
    }

    // 
    // ================
    let { width, height } = this.canvas
    let points = this.createPoints(this.num, width / 2, height / 2, this.radius)

    // 
    for (let [index, line] of lineGroup.entries()) {
      let { borderColor, borderWidth } = this.axis.polygonStyle

      line
        .attr({
          shape: {
            x1: this.canvas.width / 2,
            y1: this.canvas.height / 2,
            x2: points[index][0],
            y2: points[index][1]
          },
          style: {
            stroke: borderColor,
            lineWidth: borderWidth
          }
        })
        .animateTo({
          shape: {
            percent: 1
          }
        }, 300, 0, 'cubicIn')
    }

    // 
    for (let [index, point] of pointGroup.entries()) {
      let { radius, color, borderWidth, borderColor } = this.axis.pointStyle
      point
        .attr({
          shape: {
            r: radius
          },
          style: {
            fill: color,
            stroke: borderColor,
            lineWidth: borderWidth
          }
        })
        .animateTo({
          shape: {
            cx: points[index][0],
            cy: points[index][1]
          },
          style: {
            opacity: 1
          }
        }, 300, 0, 'cubicIn')
    }

    //              
    for (let [index, label] of labelGroup.entries()) {
      // 
      let {
        fontSize,
        fontWeight,
        fontFamily,
        fontStyle,
        color } = this.axis.labelStyle

      label
        .attr({
          style: {
            fontSize,
            fontWeight,
            fontStyle,
            fontFamily,
            textFill: color,

            textVerticalAlign: (function (that) {
              let cy = that.canvas.height / 2
              let y = points[index][1]

              if (y < cy) {
                return 'bottom'
              }
            }(this))
          }
        })
        .animateTo({
          style: {
            opacity: 1
          },
          position: [
            (function (that) {
              let labelWidth = label.getBoundingRect().width
              let cx = that.canvas.width / 2
              let x = points[index][0]

              if (x > cx) {
                return x + 10
              }
              if (x < cx) {
                return x - labelWidth - 10
              }
              if (x === cx) {
                return x - labelWidth / 2
              }
            }(this)),
            (function (that) {
              let labelHeight = label.getBoundingRect().height
              let cy = that.canvas.height / 2
              let y = points[index][1]

              if (y > cy) {
                return y + labelHeight / 2
              }
              if (y < cy) {
                return y - labelHeight / 2
              }
              if (y === cy) {
                return y
              }
            }(this))
          ]
        }, 300, 0, 'cubicIn', () => {
          this.isAnimate = false
        })
    }
  }

  /**
   * 
   * @param  {Number} index  
   * @param  {Object} option 
   * @return {void}   void
   */
  drawRadar(index, option) {
    // 
    // ===========
    let radarStyle = _.merge({
      normal: {
        color: new zrender.RadialGradient(0.5, 0.5, 0.5, [
          {
            offset: 0,
            color: zrender.color.modifyAlpha(this.colors(index + 2), 0.3)
          },
          {
            offset: 1,
            color: zrender.color.modifyAlpha(this.colors(index + 2), 0.9)
          }
        ]),
        borderColor: this.colors(index + 2),
        borderWidth: 2,
        shadowBlur: 0,
        shadowOffsetX: 0,
        shadowOffsetY: 0,
        shadowColor: 'transparent'
      },
      emphasis: {
        color: new zrender.RadialGradient(0.5, 0.5, 0.5, [
          {
            offset: 0,
            color: zrender.color.modifyAlpha(this.colors(index + 2), 0.7)
          },
          {
            offset: 1,
            color: zrender.color.modifyAlpha(this.colors(index + 2), 0.9)
          }
        ]),
        borderColor: this.colors(index + 2),
        borderWidth: 2,
        shadowBlur: 0,
        shadowOffsetX: 0,
        shadowOffsetY: 0,
        shadowColor: 'transparent'
      }
    }, option.radarStyle)

    let itemStyle = _.merge({
      radius: 4,
      color: '#FFF',
      borderColor: radarStyle.normal.color,
      borderWidth: radarStyle.normal.borderWidth
    }, option.itemStyle)
    let normal = {
      fill: radarStyle.normal.color,
      stroke: radarStyle.normal.borderColor,
      lineWidth: radarStyle.normal.borderWidth,
      shadowBlur: radarStyle.normal.shadowBlur,
      shadowOffsetX: radarStyle.normal.shadowOffsetX,
      shadowOffsetY: radarStyle.normal.shadowOffsetY,
      shadowColor: radarStyle.normal.shadowColor
    }
    let emphasis = {
      fill: radarStyle.emphasis.color,
      stroke: radarStyle.emphasis.borderColor,
      lineWidth: radarStyle.emphasis.borderWidth,
      shadowBlur: radarStyle.emphasis.shadowBlur,
      shadowOffsetX: radarStyle.emphasis.shadowOffsetX,
      shadowOffsetY: radarStyle.emphasis.shadowOffsetY,
      shadowColor: radarStyle.emphasis.shadowColor
    }
    let itemNormal = {
      fill: itemStyle.color,
      stroke: itemStyle.borderColor,
      lineWidth: itemStyle.borderWidth
    }

    let radar = this.radarContainer[index].children().filter(d => d.name === 'radar')[0]
    let pointGroup = this.radarContainer[index].children().filter(d => d.name === 'radarPoint')

    //  range
    // ===========    
    let range = []
    for (let d of option.data) {
      range.push(this.scale(d.value) / this.radius)
    }

    // 
    // =========
    let { width, height } = this.canvas
    let points = this.createPoints(this.num, width / 2, height / 2, this.radius, range)

    this.isAnimate = true
    radar
      .attr({ 
        range,
        style: normal
      })
      .animate('shape', false)
      .when(300, { points, range })
      .start('elasticOut')

    // 
    // ========================
    for (let [i, d] of points.entries()) {
      pointGroup[i]
        .attr({
          z: 2,
          shape: {
            r: itemStyle.radius
          },
          style: itemNormal
        })
        .on('mouseover', () => {
          if (!this.isAnimate) {
            radar.attr({ z: 1 })
          }
        })
        .on('mouseout', () => {
          if (!this.isAnimate) {
            radar.attr({ z: 0 })
          }
        })
        .animate('shape', false)
        .when(300, {
          cx: d[0],
          cy: d[1]
        })
        .start('elasticOut')
        .done(() => {
          this.isAnimate = false
        })
    }
    
    // 
    // ===========
    radar
      .off('mouseover')
      .off('mouseout')
      .off('mousemove')
      // tooltip 
      .on('mousemove', e => {
        // 
        let values = []
        for (let d of radar.range) {
          values.push(this.scale.invert(d * this.radius))
        }

        let names = radar.pointName

        let { titleName } = e.target
        let { offsetX, offsetY } = e

        //  tooltip   
        if (this.tooltip.show && !this.isAnimate) {
          this.tip.show({
            titleName,
            legendName: names,
            value: values,
            x: offsetX, y: offsetY
          }, this.tooltip.itemStyle, normal.fill)
        }
      })
      // 
      .on('mouseover', () => {
        if (!this.isAnimate) {
          radar
            .attr({
              z: 1,
              style: emphasis
            })
        }
      })
      .on('mouseout', () => {
        if (!this.isAnimate) {
          radar
            .attr({
              z: 0,
              style: normal
            })
        }
        //  tooltip
        this.tip.hide()
      })
  }

  /**
   * 
   * @param  {Number} name  
   * @return {void}   void
   */
  bindLegendEvent(name) {
    //  name  Group 
    let legendGroup = this.legendContainer.filter(d => d.name === name)[0]
    let radarContainer = this.radarContainer.filter(d => d.name === name)[0]
    let radar = radarContainer.children().filter(d => d.name === 'radar')[0]
    let points = radarContainer.children().filter(d => d.name === 'radarPoint')

    // 
    // ===============
    let itemFill = radar.style.fill

    // 
    // =================================
    this.legend.init(name, itemFill)      
    for (let point of points) {
      point.show()
    }

    // 
    // ================
    this.buffer = deepClone(this.series)

    // 
    // ==========
    legendGroup
      .off('click')
      .on('click', () => {
        switch (legendGroup.status) {
        case 'normal':
          // 
          this.legend.toggle(name, itemFill)

          // 1.
          for (let d of this.buffer.filter(item => item.name === name)[0].data) {
            d.value = 0
          }

          // 2. 
          for (let [i, d] of this.buffer.entries()) {
            this.drawRadar(i, d)
          }

          // 3.         
          for (let point of points) {
            point.hide()
          }
          break
        case 'blur':
          // 
          this.legend.toggle(name, itemFill)       

          // 1. 
          for (let [i, d] of this.series.filter(item => item.name === name)[0].data.entries()) {
            this.buffer.filter(item => item.name === name)[0].data[i].value = d.value
          }

          // 2. 
          for (let [i, d] of this.buffer.entries()) {
            this.drawRadar(i, d)
          }

          // 3. 
          for (let point of points) {
            point.show()
          }
          break
        default:
          console.log('work')
        }
      })
  }

  /**
   * 
   * @param  {Object} option 
   * @return {void}   void
   */
  setOption(option) {
    // 
    // ======================
    if (!this.isInit) {
      this.initConfig(option)
      this.initGroup(option)
      this.initAxis()     
    }

    this.series = option.series

    // 
    // ==========
    this.scale = this.createScale(option.series, option.min, option.max)

    // 
    // =========
    for (let [i, d] of option.series.entries()) {
      this.drawRadar(i, d)
    }

    // 
    // ==============
    if (this.legendConfig.show) {
      for (let group of this.legendContainer) {
        this.bindLegendEvent(group.name)
      }
    }

    // 
    // =================
    this.isInit = true
  }
}



