import './zoom.css'
// import config from '@/config'
import $ from 'jquery'

const body = $('body')

let lastScale = [1, 1]

/**
 *  
 */
export default function zoom(width, height) {
  const x = window.innerWidth / width
  const y = window.innerHeight / height
  lastScale = [x, y]
  body
    .css('transform',`scale(${x}, ${y})`)
 
}

/**
 *  zoom
 */
export function adaptZoom(width, height) {
  body
    .addClass('zoomify')
    .css('width', width)
    .css('height', height)
}

/**
 *  zoom
 *  @param    {Array.number}  origin 
 *  @return   {Array.number}  zoom
 */
export function originToDeformation(origin) {
  return [origin[0] * lastScale[0], origin[1] * lastScale[1]]
}

/**
 *  zoom
 *  @param    {Array.number}  deformation zoom
 *  @return   {Array.number}  
 */
export function deformationToOrigin(deformation) {
  return [deformation[0] / lastScale[0], deformation[1] / lastScale[1]]
}

/**
 * 
 * @return {Array} 
 */
export function getRate() {
  return lastScale
}



