let config = {}

export default {
  /**
   *  
   *  @param    {Object}  srcConfig 
   */
  merge(srcConfig) {
    config = {
      ...config,
      ...srcConfig
    }
  },
  /**
   *  
   *  @param    {string}  key key
   *  @return   {*}  
   */
  get(key) {
    return config[key]
  }
}


